import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import swal, { SweetAlertOptions } from 'sweetalert2';
import { ProductService } from "../../services/product.service";

@Component({
	selector: 'app-cart',
	templateUrl: './cart.component.html',
	styleUrls: ['./cart.component.css']
})
export class CartComponent implements OnInit {

	products;
	total: number;
	fileUrl = Config.FileUrl;
	product_count;


	constructor(private productService:ProductService, private httpService:HttpService, private router:Router, private route:ActivatedRoute) { }

	ngOnInit() {
		this.getCart();
	}


	//html內容改變時
	ngAfterContentChecked()  {
		//計算總和
	    this.total = Object.keys(this.products).reduce(
	    	(runningValue: number, key)=> {
	      		return runningValue = runningValue + (this.products[key].price * this.products[key].quantity);
	    	}, 0);
	}


	//檢查庫存
	checkQuantity(quantity, product_quantity){
		
		if(Number(quantity) > Number(product_quantity))	{
			return true;
		}
		else{
			return false;
		}
	}


	//刪除商品
	deleteItem(pId) {
		let url = `${Config.APIUrl}/api/shop/cart/product?product_id=${pId}`;
		this.httpService.deleteHttp(url).subscribe(
			res => {
				this.productService.getCartNumber();
				this.products = res.products;
			}
		);
	}


	//取得購物車內容
	getCart(){
		let url = `${Config.APIUrl}/api/shop/cart`;
		this.httpService.getHttp(url).subscribe(
			res => {
				this.productService.getCartNumber();
				this.products = res.products;
	
			}
		);
	}


	//變更購買數量
	changeQuantity(id, quantity) {
		
		if(quantity){

			if(quantity<=0){
				return;
			}

			let params:FormData = new FormData();
			params.append('product_id', id);
			params.append('quantity', quantity);
			params.append('_method', "PUT");

			
			let url = `${Config.APIUrl}/api/shop/cart/product`;
			this.httpService.postHttp(url, params).subscribe(
				res => {
					this.productService.getCartNumber();
					this.products = res.products;
				}


			)
		}

	}


	//檢查是否可以送出
	checkCart() {
		let filter_cart = Object.keys(this.products).filter(
			key => ( Number(this.products[key].quantity) > Number(this.products[key].product_now_quantity) || this.products[key].status!=1 || Number(this.products[key].quantity<=0 ))

		);
		console.log(filter_cart);
		return filter_cart.length;
	}


	//送出購物車
	sentCart(){
		//重新讀取購物車內容
		//let url = `${Config.APIUrl}/api/shop/cart`;
		//this.httpService.getHttp(url).subscribe(
			//res => {
				//this.products = res.products;

				//檢查
				const check = this.checkCart();
				if(check!=0)
					return;

				//進入下一步
				this.router.navigate(['../order_edit'],  { relativeTo: this.route });
			//}
		//);
		
	}

}
