import { Component, OnInit, Input } from '@angular/core';
import { TokenService } from "../../auth/token.service";
import { CookieService } from '../../services/cookie.service';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import * as $ from 'jquery';
import swal, { SweetAlertOptions } from 'sweetalert2';
import { ProductService } from "../../services/product.service";
import { MemberService } from "../../member/member.service"; 

@Component({
	selector: 'app-cart-add',
	templateUrl: './cart-add.component.html',
	styleUrls: ['./cart-add.component.scss']
})
export class CartAddComponent implements OnInit {

	//input 商品ID
	@Input() input_pId;
	@Input() add_check;
	@Input() product_type;
	@Input() add_status;
	product_number = 1;
	show_status;

	constructor(private memberService:MemberService, private productService:ProductService, private tokenService:TokenService, private cookieService:CookieService, private httpService:HttpService) { }

	ngOnInit() {
		this.show_status = this.add_status;
		//this.checkBought();
	}


	//檢查是否可以繼續購買
	checkBought() {
		console.log(this.show_status);

		//this.tokenService.loginCheck().subscribe(
		//	res => {
				//登入狀態
				if(this.memberService.user){
					let url = `${Config.APIUrl}/api/shop/my-product/check-if-buy?id=${this.input_pId}`;
					this.httpService.getHttp(url).subscribe(
						res => {
							this.show_status = res.check_result;
							console.log(this.show_status);
						}
					);

				//未登入 都可以加入購物車
				}else{
					this.show_status = 3;
					console.error(this.show_status);
				}
			//}
		//);
	}


	//加入購物車
	add() {

		//this.tokenService.loginCheck().subscribe(
			//res => {
				//登入狀態
				if(this.memberService.user){
					console.log("login");
					let cart = {};
					cart[`${this.input_pId}`] = this.product_number;

					let params:FormData = new FormData();
					params.append('products', JSON.stringify(cart));

					let url = `${Config.APIUrl}/api/shop/cart/merge`;

					this.httpService.postHttp(url, params).subscribe(
						res => {
							//更新購物車數量
							this.productService.getCartNumber();
							swal({
								title: '操作成功!',
								text: '加入購物車成功',
								type: 'success',
							});
						}
					);



				//未登入狀態
				}else{
					console.log(this.input_pId);

					let cart = this.cookieService.getCookie('cart');
					let cartArray = {};

					if(cart)
						cartArray = JSON.parse(cart);
					
					//更新或者增加數量
					if(this.add_check){
						if(cartArray[`${this.input_pId}`])
							cartArray[`${this.input_pId}`] += this.product_number;
						else
							cartArray[`${this.input_pId}`] = this.product_number;
					}else{
						cartArray[`${this.input_pId}`] = 1;
					}
					
					
					console.log(cartArray);
					//console.log(JSON.stringify(cartArray));
					this.cookieService.setCookie('cart',JSON.stringify(cartArray));

					cart = this.cookieService.getCookie('cart');
					console.log(cart);

					//更新購物車數量
					this.productService.getCartNumber();
					swal({
						title: '操作成功!',
						text: '加入購物車成功',
						type: 'success',
					});

				}
			}
		//);
	//}

	more(){
		this.product_number += 1;
	}

	less(){
		if(this.product_number >= 2){
			this.product_number -= 1;
		}
	}

}
