import { Component, OnInit, Inject } from '@angular/core';
import { NgForm, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { HttpService } from '../../services/http.service';
import { CookieService } from '../../services/cookie.service';
import { ProductService } from '../../services/product.service';
import { TokenService } from '../token.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import  * as Config from '../../models/config.model';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import { MemberService } from "../../member/member.service";
import { RegisterComponent } from "../register/register.component";
import { ForgetPwComponent } from "../forget-pw/forget-pw.component";

export interface DialogData {
	returnUrl:string
}

@Component({
	selector: 'app-login',
	templateUrl: './login.component.html',
	styleUrls: ['./login.component.scss']
})

export class LoginComponent implements OnInit {

	loginForm = this.formBuilder.group({
		username: ['',Validators.required],
		password: ['',Validators.required],
	});

	
  	token:string;
  	submitted = false;
	returnUrl: string;

	constructor(
		public dialogRef: MatDialogRef<LoginComponent>,
    	@Inject(MAT_DIALOG_DATA) public data: DialogData, 
    	private dialog:MatDialog,
    	private route: ActivatedRoute, 
    	private tokenService:TokenService, 
    	private httpService:HttpService, 
    	private router:Router, 
    	private cookieService:CookieService, 
    	private formBuilder: FormBuilder, 
    	private productService:ProductService,
    	private memberService:MemberService,
    ) { }


	ngOnInit() {
		/* get return url from route parameters or default to '/'
		this.route.params.subscribe(params => {
			this.returnUrl = params['returnUrl'] || '/';
			console.log(this.returnUrl);
		});
        */
		//this.isLogin();
	}


	// convenience getter for easy access to form fields
	get username() { return this.loginForm.get('username'); }
	get password() { return this.loginForm.get('password'); }


	//註冊
	register() {
		this.dialogRef.close();
		//this.router.navigate(['/register']);
		this.openDialog();
	}


	//忘記密碼
	forget() {
		this.dialogRef.close();
		//this.router.navigate(['/register']);
		const dialogRef = this.dialog.open(ForgetPwComponent, {
		  	width: '588px',
		});
	}


	//開啟pop up
    openDialog(){
    	const dialogRef = this.dialog.open(RegisterComponent, {
		  	width: '588px',
		});
    }


	//判斷是否login
	isLogin() {
		this.tokenService.tokenState()
			.subscribe(res =>{
				if(res)
					this.router.navigate(['/home']);
			});
	}


    //submit form
	onSubmit() {

		//判斷是否已按了送出 按了就要顯示驗證訊息
		this.submitted = true;

		//表單沒有驗證成功 沒有動作
		if(this.loginForm.invalid){
			return;
		}

  		let params:FormData = new FormData();
  		Object.keys(this.loginForm.value).map(key=> params.append(key,this.loginForm.value[key]));
  
  		const url = `${Config.APIUrl}/api/auth/login`;

	  	this.httpService.postHttp(url,params)
		    .subscribe(res => {
		    	console.log(res);
		    	
			    //login success, save token in cookies
			    this.cookieService.setCookie('token',res.token); 
			    this.cookieService.setCookie('userid',res.user_info.id); 
			    this.cookieService.setCookie('level',res.user_info.level);
				this.cookieService.setCookie('name',res.user_info.name);
				this.cookieService.setCookie('avatar',res.user_info.avatar);
			    this.token = res.token;

			    //移轉cart
			    this.productService.mergeCookieCart();
			    this.productService.getCartNumber();
			    //更新會員資料
			    this.memberService.setUserLogin(res);

			    //this.router.navigate(['/home']);
			    this.dialogRef.close();
			    this.router.navigateByUrl(this.data.returnUrl);
		    	
		    });
	}

}
