import { Component, OnInit, Input } from '@angular/core';
import { HttpService } from '../../services/http.service';
import { CookieService } from '../../services/cookie.service';
import { ProductService } from '../../services/product.service';
import { MemberService } from "../../member/member.service";
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material'; //popup dialog
import { FacebookRegisterComponent } from '../facebook-register/facebook-register.component';
import { LoginComponent } from '../login/login.component';
import {
    AuthService,
    FacebookLoginProvider,
    GoogleLoginProvider
} from 'angular-6-social-login';
import  * as Config from '../../models/config.model';
declare var FB: any;

@Component({
  selector: 'app-facebook',
  templateUrl: './facebook.component.html',
  styleUrls: ['./facebook.component.scss']
})


export class FacebookComponent implements OnInit {

	user_id:string;
	@Input() InputReturnUrl;

	constructor(private memberService:MemberService, public dialogRef: MatDialogRef<LoginComponent>, private productService:ProductService, private httpService:HttpService, private router:Router, private cookieService:CookieService, public dialog: MatDialog, private socialAuthService: AuthService) { }

	ngOnInit() {}


	public socialSignIn(socialPlatform : string) {
	    let socialPlatformProvider;
	    if(socialPlatform == "facebook"){
	      	socialPlatformProvider = FacebookLoginProvider.PROVIDER_ID;
	    }
	    
	    this.socialAuthService.signIn(socialPlatformProvider).then(
	      	(userData) => {
	        	console.log(socialPlatform+" sign in data : " , userData);
	        	
	            //進行系統登入
	            this.dialogRef.close();
	            this.loginSystem(userData);
	      	}
	    );
	}


    loginSystem(user_data){
    	//login system api
		let params:FormData = new FormData();
		params.append('social_id',user_data.id);
		params.append('provider','FB');
		console.log(this.user_id);

		const url = `${Config.APIUrl}/api/auth/social-login`;

		this.httpService.postHttp(url,params)
			.subscribe(res => {
				console.log(res);
				if(res.success_code){
					//尚未註冊 進行註冊
					if(res.success_code=='PLEASE_REGISTER'){
						//this.registerSystem(this.user_id,this.openDialog());
						console.log("need to register");
						this.dialogRef.close();
						this.openDialog(user_data);
					}

				//login success, save token in cookies
				}else{				
	    			this.cookieService.setCookie('token',res.token); 
	    			this.cookieService.setCookie('level',res.user_info.level);
					this.cookieService.setCookie('name',res.user_info.name);
					this.cookieService.setCookie('avatar',res.user_info.avatar);
					//this.router.navigate(['/home']);

					//移轉cart
			    	this.productService.mergeCookieCart();
			    	this.memberService.setUserLogin(res);

					this.router.navigateByUrl(this.InputReturnUrl);
			    }
			});
    }


    //開啟pop up
    openDialog(user_data){
    	const dialogRef = this.dialog.open(FacebookRegisterComponent, {
		  	width: '500px',
		  	data: {name: user_data.name, id: user_data.id, image: user_data.image, email: user_data.email}
		});
    }


    

  
}
