import { Component, OnInit } from '@angular/core';
import { CookieService } from '../../services/cookie.service';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import { MemberService } from "../../member/member.service";
import { ProductService } from '../../services/product.service';


@Component({
	selector: 'app-logout',
	templateUrl: './logout.component.html',
	styleUrls: ['./logout.component.css']
})


export class LogoutComponent implements OnInit {

	constructor(private productService:ProductService, private cookieService:CookieService, private httpService:HttpService, private router:Router, private memberService:MemberService) { }

	ngOnInit() {
	}


	logout() {

		
		
		 
		const url = `${Config.APIUrl}/api/auth/logout`;

	  	this.httpService.postHttp(url,null)
		    .subscribe(res => {
		    	console.log(res);
		    	this.cookieService.deleteCookie('token');
				this.cookieService.deleteCookie('name');
				this.cookieService.deleteCookie('userid');
				this.cookieService.deleteCookie('avatar');
				this.cookieService.deleteCookie('level');
				this.cookieService.deleteCookie('cart');
				
				this.memberService.user = null;
				this.memberService.mailCount = null;
				this.productService.getCartNumber();
				this.router.navigate(['/']);
				    	
		    });
		

	}

}
