import { Injectable } from '@angular/core';
import { CookieService } from '../services/cookie.service';
import { HttpService } from '../services/http.service';
import  * as Config from '../models/config.model';
import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import { HttpClient, HttpHeaders, HttpEvent } from '@angular/common/http';
import swal, { SweetAlertOptions } from 'sweetalert2';

@Injectable({
	providedIn: 'root'
})
export class TokenService {

	token_state:number = 0;


	constructor(private cookieService:CookieService, private httpService:HttpService, private http:HttpClient) { }


	//guard 驗證是否有填完整資料 以及是否有登入
	infoState():Observable<string>{
		const token = this.cookieService.getCookie('token');
		//let para = new FormData();
		//para.append('token',token);

		const url = `${Config.APIUrl}/api/user/check-info-complete`;

		return this.http.get<any>(url).pipe(
			map(res => {
				//this.token_state = res.body.result;
				console.warn(res);

				//回傳為沒填完整資料 sucees_code存在代表不是success 方案
				if(res.success_code){
					switch (res.success_code) {
						case "BLOCK":
					
							swal({
								title: 'Error!',
								text: '需填寫完整資料',
								type: 'error',
								confirmButtonText: 'YES'
							});
							break;

						case "NO_LOGIN_PERMISSION":
							swal({
								title: 'Error!',
								text: '你的帳號尚未有登入權限',
								type: 'error',
								confirmButtonText: 'YES'
							});
							break;
						
						default:
							// code...
							break;
					}
					return res.success_code;
				}else{
					
					return 'SUCCESS';
				}
			}),
			
		);
	}

	//guard 驗證是否有登入用
	tokenState():Observable<boolean>{
		const token = this.cookieService.getCookie('token');
		let para = new FormData();
		para.append('token',token);

		const url = `${Config.APIUrl}/api/auth/refresh-return-success`;
		//const url = `${Config.APIUrl}/api/auth/check`;

		return this.http.post<any>(url, para).pipe(
			
			map(res => {
				//this.token_state = res.body.result;
				console.warn(res);
				//回傳參數有token表示成功
				if(res.token){
					this.cookieService.setCookie('token',res.token);
					this.cookieService.setCookie('level',res.level);
					this.cookieService.setCookie('name',res.name);
					this.cookieService.setCookie('avatar',res.avatar);
					return true;

				//回傳參數沒有token表示失敗
				}else{
					//沒登入權限 需跳出警示窗
					if(res.success_code == 'NO_LOGIN_PERMISSION'){
						swal({
							title: 'Error!',
							text: '你的帳號尚未有登入權限',
							type: 'error',
							confirmButtonText: 'YES'
						});
					}
					console.warn(this.token_state);
					console.warn(res);
					return false;
				}
			}),
			
		);

	}


	//intercept 更新token用
	tokeRefresh(){
		const token = this.cookieService.getCookie('token');
		let para = new FormData();
		para.append('token',token);
		const url = `${Config.APIUrl}/api/auth/refresh`;

		return this.httpService.postHttp(url, para).pipe(
	    	map(res => {
				//console.log('didn\'t get token in func');
	    		if(res.token){
					//console.log('get token in func');
	    			this.cookieService.setCookie('token',res.token);
	    			this.cookieService.setCookie('level',res.level);
					this.cookieService.setCookie('name',res.name);
					this.cookieService.setCookie('avatar',res.avatar);
	      			return res; //return the new authorization token
	      		}
	    	})
	    );
	}


	//用在component裡面 檢查是否有登入
	loginCheck():Observable<boolean>{
		const token = this.cookieService.getCookie('token');
		let para = new FormData();
		para.append('token',token);

		const url = `${Config.APIUrl}/api/auth/check`;

		return this.http.post<any>(url, para).pipe(
			
			map(res => {
				//this.token_state = res.body.result;
				console.warn(res);
				//回傳參數有token表示成功
				if(res.token){
					this.cookieService.setCookie('token',res.token);
					this.cookieService.setCookie('level',res.level);
					this.cookieService.setCookie('name',res.name);
					this.cookieService.setCookie('avatar',res.avatar);
					return true;

				//回傳參數沒有token表示失敗
				}else{
					//沒登入權限 需跳出警示窗
					if(res.success_code == 'NO_LOGIN_PERMISSION'){
						swal({
							title: 'Error!',
							text: '你的帳號尚未有登入權限',
							type: 'error',
							confirmButtonText: 'YES'
						});
					}
					console.warn(this.token_state);
					console.warn(res);
					return false;
				}
			}),
			
		);

	}


	
}
