import { Injectable } from '@angular/core';
import { HttpService } from './http.service';
import { CookieService } from './cookie.service';
import  * as Config from '../models/config.model';
import { catchError, map, tap, retry, reduce } from 'rxjs/operators';
import { TokenService } from "../auth/token.service";

@Injectable({
	providedIn: 'root'
})

export class ProductService {

	number;
	constructor(private http:HttpService, private cookieService:CookieService, private tokenService:TokenService) { }


	//取得購物車 數量
	getCartNumber(){
		this.tokenService.loginCheck().subscribe(
			res => {
				//登入狀態
				if(res){
					let url = `${Config.APIUrl}/api/shop/cart`;
					this.http.getHttp(url).subscribe(
						res => {
							this.number = res.sum_quantity;
						}
					);

				//not login
				}else{

					let cart = this.cookieService.getCookie('cart');
					let cartArray = {};
					if(cart)
						cartArray = JSON.parse(cart);

					//計算總和
				    this.number = Object.keys(cartArray).reduce(
				    	(runningValue: number, key)=> {
				      		return runningValue = runningValue + cartArray[key];
				    	}, 0);
				}
			}
		);
	}


	//取得商品列表
	getProductList(paramObject) {
		let url:string;
		//console.log(paramObject);

		let param = this.http.getParamFormat(paramObject);
		url = `${Config.APIUrl}/api/shop/product?${param}`;
		return this.http.getHttp(url);

	}


	//將cookie購物車內容合併至DB
	mergeCookieCart() {

		const cart  = this.cookieService.getCookie('cart');

		if(cart){
			let url:string;
			let params:FormData = new FormData();
			params.append('products', cart);
			url = `${Config.APIUrl}/api/shop/cart/merge`;

			this.http.postHttp(url, params).subscribe(
				res => {
					this.cookieService.deleteCookie('cart');
				}
			);
		}
	}
}
