import { Injectable } from '@angular/core';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import {
	HttpEvent, 
	HttpInterceptor, 
	HttpHandler, 
	HttpRequest,
	HttpResponse,
	HttpErrorResponse,
} from '@angular/common/http'
import { Observable, throwError } from 'rxjs';
import { catchError,map, switchMap} from 'rxjs/operators';
import { Location } from '@angular/common'; //接收route para
import swal, { SweetAlertOptions } from 'sweetalert2';
import { TokenService } from '../auth/token.service';
import { CookieService } from '../services/cookie.service';
import { HttpService } from '../services/http.service';
import * as Config from '../models/config.model';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material'; //popup dialog
import { LoginComponent } from '../auth/login/login.component';


@Injectable()

export class ResponseInterceptor implements HttpInterceptor {


	constructor(private dialog:MatDialog, private router:Router, private tokenService: TokenService, private cookieService:CookieService, private httpService:HttpService) { }


	public intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

		console.log("response.interceptor.ts");

		return next.handle(req).pipe(

			//進行response data處理
			map(event => {
				
				//如果event為http response class
				if (event instanceof HttpResponse) { 

					//有回傳資料的情況
					if (event.body) {

						//googel search 不處理 直接成功回傳結果
						let reg = RegExp('^https:\/\/www.googleapis.com\/customsearch\/v1');
						if(reg.test(event.url)){
							console.log('google');
							console.log(event);
							return event;
						
						//如果response event的結果是失敗
						}else if(event.body.status!=true){
							console.warn(`response error:`);
							console.log(event);
							if(event.body.err_detail)
								console.warn(event.body.err_detail);
							//sweetalert token失效不做alert
							if(event.body.error_code != 'TOKEN_INVALID'){
								let err_msg = event.body.err_msg;
								swal({
								  title: 'Error!',
								  text: err_msg,
								  type: 'error',
								  confirmButtonText: 'YES'
								});

								return Observable.create(observer => observer.error(event));
							}
						
						//respone 成功
						}else{
							console.log(`response success:`);
							//console.log(event);
							
							//如果sucess code = success 只截取data內容 直接進行回傳結果前處理
							 
							if(event.body.data && event.body.success_code=='SUCCESS'){
								let detail = event.body.data;
								event = event.clone({ body: detail});
								console.log(event);
								
							}
							
							return event;
						}
					}
				}  
				return event;						
			}),

			//錯誤處理 返回登入畫面 
			
			catchError(res => {
				const err_code = (<HttpErrorResponse>res).error.err_code;
				const err_msg = (<HttpErrorResponse>res).error.err_msg;
				const err_detail = (<HttpErrorResponse>res).error.err_detail;

                switch (res.status) {
                    //權限問題 token
                    case 419: 
                    	
						if(err_code != 'TOKEN_EXPIRE'){
	                       	console.log('response error:419 權限問題/'+err_msg);
							console.warn(err_detail);
	                       	//沒登入權限 需跳出警示窗
							if(err_code == 'NO_LOGIN_PERMISSION'){
								swal({
									title: 'Error!',
									text: err_msg,
									type: 'error',
									confirmButtonText: 'YES'
								}).then((success)=>{
									this.cookieService.deleteCookie('token');
									//this.router.navigate(['/login']);
									this.openDialog('/');
								});

							//token 失效 直接到登入頁
							}else{
								//this.router.navigate(['/login']);
								this.openDialog('/');
							}
						}
						
                    break;


                    
                    case 422: //欄位有誤
                    case 403: //操作錯誤
                    case 500: //其他問題
                    	console.warn('err_msg:'+res.error.err_msg);
                    	console.warn(res.error.err_detail);

							swal({
								title: 'Error!',
								text: err_msg,
								type: 'error',
								confirmButtonText: 'YES'
							});
						
                    break;

                    case 404:
                    	this.router.navigate(['/error404']);
                    break;

                    
                    


                    
                }
                // 以错误的形式结束本次请求
				return Observable.create(observer => observer.error(res));
                
            })
            
		);
	}



	//開啟pop up
    openDialog(data){
    	const dialogRef = this.dialog.open(LoginComponent, {
		  	width: '500px',
		  	data: { returnUrl: data}
		});
    }

}