import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';

@Component({
	selector: 'app-google-search',
	templateUrl: './google-search.component.html',
	styleUrls: ['./google-search.component.scss']
})
export class GoogleSearchComponent implements OnInit {

	search;
	constructor(private httpService:HttpService) { }

	ngOnInit() {

	}


	getGSearchResult(){
		let url = `https://www.googleapis.com/customsearch/v1?key=${Config.GoogleSearchKey}&cx=${Config.GoogleSearchID}&q=${this.search}`;

		this.httpService.getHttp(url).subscribe(
			res => console.log(res)
		);
	}



}
