import { Injectable } from '@angular/core';
import * as Config from "../models/config.model";
import { HttpService } from '../services/http.service';
import { LayoutService } from '../services/layout.service';

@Injectable({
	providedIn: 'root'
})
export class WriterService {

	writer;
	list = {};
	articles;

	constructor(private httpService:HttpService, private layoutService:LayoutService) { }


	//單一作者資訊
	getWriterInfo(id){
		let paramObject = {
			'id':id,
			'viewed':1,
		};
		let param = this.httpService.getParamFormat(paramObject);
		let url = `${Config.APIUrl}/api/writer/show?${param}`;
		this.httpService.getHttp(url).subscribe(
			res => {

				console.log(res);
				this.writer = res.writer;

				this.layoutService.setMetaData(res.writer.metadata);
			}
		);
	}


	//作者相關文章
	getWriterArticle(writer_id, from, limit){
		let paramObject = {
			'id':writer_id,
			'from':from,
			'limit':limit
		};
		let param = this.httpService.getParamFormat(paramObject);
		let url = `${Config.APIUrl}/api/writer/articles?${param}`;
		this.httpService.getHttp(url).subscribe(
			res => {

				console.warn(res);
				this.articles = res.articles;
			}
		);
	}


	//作者相關商品
	getWriterProduct(writer_id, product_type, from, limit){
		let paramObject = {
			'id':writer_id,
			'type':product_type,
			'from':from,
			'limit':limit,
			'sort':'post_date',
			'sort_order':'desc',
		};
		let param = this.httpService.getParamFormat(paramObject);
		let url = `${Config.APIUrl}/api/writer/products?${param}`;
		this.httpService.getHttp(url).subscribe(
			res => {

				console.log(res);
				if(product_type=='e-book')
					this.list['ebook'] = res.products;
				else
					this.list[product_type] = res.products;
			}
		);
	}
}
