import { Component, OnInit, Input } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import { CookieService } from '../../services/cookie.service';
import { LayoutService } from '../../services/layout.service';

@Component({
	selector: 'app-product',
	templateUrl: './product.component.html',
	styleUrls: ['./product.component.css']
})
export class ProductComponent implements OnInit {

	product;
	sliders;
	fileUrl = Config.FileUrl;

	constructor(protected httpService:HttpService, protected route:ActivatedRoute, protected cookieService:CookieService, private layoutService:LayoutService) 
	{ 
		//this.layoutService.setMeta();
	}

	ngOnInit() {
		this.route.params.subscribe(params => {
    		this.getProduct(params['id']);
    		this.getSliders(params['id']);
    	});
	}


	//取得商品資訊
	getProduct(id){
		let url = `${Config.APIUrl}/api/shop/product/show?id=${id}&viewed=1`;
		this.httpService.getHttp(url).subscribe(
			res => {
				this.product = res.product;
				this.layoutService.setMetaData(res.product.metadata);
			}
		);
	}


	//取得商品幻燈片
	getSliders(id){
		let url = `${Config.APIUrl}/api/shop/product/sliders?id=${id}`;
		this.httpService.getHttp(url).subscribe(
			res => {
				this.sliders = res.sliders;
			}
		);
	}

}
