import { Component, OnInit, Input } from '@angular/core';
import { WriterService } from "../../writer/writer.service";
import * as Config from "../../models/config.model";

@Component({
	selector: 'app-single-writer',
	templateUrl: './single-writer.component.html',
	styleUrls: ['./single-writer.component.css']
})
export class SingleWriterComponent implements OnInit {

	fileUrl = Config.FileUrl;
	@Input() writer_id;

	constructor(public writerService:WriterService) { }

	ngOnInit() {
		if(this.writer_id)
			this.writerService.getWriterInfo(this.writer_id);
	}

}
