import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import * as $ from 'jquery';

@Component({
	selector: 'app-category',
	templateUrl: './category.component.html',
	styleUrls: ['./category.component.scss']
})
export class CategoryComponent implements OnInit {

	categories;
	@Output() childEvent = new EventEmitter<any>(); //提供Output裝飾器，讓該事件成為父親模板的事件

	constructor(private http:HttpService, private router:Router, private route:ActivatedRoute) { }

	ngOnInit() {
		this.getCategoryList();
	}


	//output id
	onClicked($event) {
		console.log($event);
		//output data
		this.childEvent.emit($event);
	}


	//取得類別列表
	getCategoryList() {
		let url:string;

		const paramObject = {
			'website_id': 1,
			'parent_id': 0,
		};
		//console.log(paramObject);

		let param = this.http.getParamFormat(paramObject);
		url = `${Config.APIUrl}/api/content/category?${param}`;
		this.http.getHttp(url).subscribe(
			res => {
				this.categories = res.categories;
				console.log(res);

				this.uiControl();
			}
		);

	}


	// ui jquery
	uiControl(){
		$(document).ready(function(){
			// 主類別
      $('.category').click(function(){
      	$('.sub-categories').hide();
      	$(this).siblings('.sub-categories').css('display','flex');

      	$('.category').removeClass('active');
      	$('.sub-category').removeClass('active');
      	$(this).addClass('active');
      })

      // 子類別
      $('.sub-category').click(function(){
      	$('.sub-category').removeClass('active');
      	$(this).addClass('active');
      })
    });
	}

}
