import { Component, OnInit,  ViewChild, AfterViewInit  } from '@angular/core';
import { NgForm, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { HttpService } from '../../services/http.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import  * as Config from '../../models/config.model';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import { formatDate } from '@angular/common';
import swal, { SweetAlertOptions } from 'sweetalert2';


@Component({
	selector: 'app-register',
	templateUrl: './register.component.html',
	styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {


	submitted = false;
	pw_value = null; //ngmodel for pw
	pw_confirm = null; //ngmodel for pw
	birthday = null; //ngmodel for date
	strFormChild: Date; //<==兒子datepicker的回傳值的顯示屬性
	recaptcha; //google 機器人
	recaptcha_key = Config.GoogleRecaptcha;

	registerForm = this.formBuilder.group({
		username: ['',[Validators.required, Validators.minLength(6)]],
		password: ['',[Validators.required, Validators.minLength(6)]],
		password_confirmation: ['',Validators.required],
		name: ['',Validators.required],
		gender: ['',Validators.required],
		email: ['',[Validators.required, Validators.pattern('^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\\.[a-zA-Z0-9_-]+)+$')]],
		country: [''],
		address: [''],
		birth: [''],
		phone: [''],
		sexual: ['', Validators.required],
		family: ['', Validators.required],
		agreement : [false, Validators.pattern('true')],
	});

	// convenience getter for easy access to form fields
	get username() { return this.registerForm.get('username'); }
	get password() { return this.registerForm.get('password'); }
	get password_confirmation() { return this.registerForm.get('password_confirmation'); }
	get name() { return this.registerForm.get('name'); }
	get birth() { return this.registerForm.get('birth'); }
	get gender() { return this.registerForm.get('gender'); }
	get email() { return this.registerForm.get('email'); }
	get country() { return this.registerForm.get('country'); }
	get address() { return this.registerForm.get('address'); }
	get phone() { return this.registerForm.get('phone'); }
	get sexual() { return this.registerForm.get('sexual'); }
	get family() { return this.registerForm.get('family'); }
	get agreement() { return this.registerForm.get('agreement'); }

	constructor(public dialogRef: MatDialogRef<RegisterComponent>, private httpService:HttpService, private router:Router, private formBuilder: FormBuilder) { }

	ngOnInit() { }


	//[datepicker] Child模板事件發生時，會呼叫此方法回傳值
	onListenChild($event: any) {
	    this.strFormChild = $event as Date;
	    this.birthday = formatDate(this.strFormChild, 'yyyy-MM-dd', 'en-US');
	}


	//submit form
	onSubmit() {

		//判斷是否已按了送出 按了就要顯示驗證訊息
		this.submitted = true;

		//表單沒有驗證成功 沒有動作
		if(this.registerForm.invalid || this.pw_value!=this.pw_confirm || !(this.recaptcha)){
			return;
		}

  		let params:FormData = new FormData();
  		Object.keys(this.registerForm.value).map(key=> params.append(key,this.registerForm.value[key]));
  		params.append('g-recaptcha-response' ,this.recaptcha);

  		const url = `${Config.APIUrl}/api/auth/register/general`;

	  	this.httpService.postHttp(url,params)
		    .subscribe(res => {
		    	
			    swal({
					title: '註冊成功!',
					text: '請收取認證信，完成帳號驗證',
					type: 'success',
				}).then(
					(success)=> {
					this.dialogRef.close();
			    	this.router.navigate(['/']);
				});
				
		    });
	}


	//google機器人
	resolved(captchaResponse: string) {
        console.log(`Resolved captcha with response ${captchaResponse}:`);
        this.recaptcha = captchaResponse;
    }

}
