import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import { CookieService } from '../../services/cookie.service';
import { MemberService } from "../../member/member.service";
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material'; //popup dialog
import { LoginComponent } from '../../auth/login/login.component';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para

@Component({
	selector: 'app-header',
	templateUrl: './header.component.html',
	styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

	user;
	config = Config;

	constructor(private router:Router, private route: ActivatedRoute, public cookieService:CookieService, private httpService:HttpService, public memberService:MemberService, private dialog:MatDialog) { }

	ngOnInit() {
		this.user = this.cookieService.getCookie('name');
		if(this.user)
			this.memberService.getUser();
		
	}


	login() {
		console.log(this.router.url);
		this.openDialog(this.router.url);
	}

	//開啟pop up
    openDialog(data){
    	const dialogRef = this.dialog.open(LoginComponent, {
		  	width: '500px',
		  	data: { returnUrl: data}
		});
    }

}
