import { Component, 
	OnInit, 
  	ChangeDetectionStrategy,
  	ViewChild,
  	TemplateRef,
  	Input,
  	Output,
  	EventEmitter,
  	OnChanges, 
  	SimpleChange 
} from '@angular/core';
import {
  	CalendarEvent,
  	CalendarEventAction,
  	CalendarEventTimesChangedEvent,
  	CalendarView,
  	CalendarDateFormatter,
  	DAYS_OF_WEEK
} from 'angular-calendar';
import { CustomDateFormatter } from './custom-date-formatter.provider';

//加入語系黨
import { registerLocaleData } from '@angular/common';
import localeZh from '@angular/common/locales/zh';
registerLocaleData(localeZh, 'zh');

@Component({
	selector: 'app-calendar',
	templateUrl: './calendar.component.html',
	styleUrls: ['./calendar.component.css'],
	providers: [
	    {
	      provide: CalendarDateFormatter,
	      useClass: CustomDateFormatter
	    }
  	]
})

export class CalendarComponent implements OnChanges {

	@Input() inputCalendarEvents;	
	@Output() childEvent = new EventEmitter<any>(); //提供Output裝飾器，讓該事件成為父親模板的事件

	view: CalendarView = CalendarView.Month;
	CalendarView = CalendarView;
	locale: string = 'zh';
	viewDate: Date = new Date();
	weekStartsOn: number = DAYS_OF_WEEK.MONDAY;
  	weekendDays: number[] = [DAYS_OF_WEEK.FRIDAY, DAYS_OF_WEEK.SATURDAY];
  	//預設測試資料
	events = [
		{
			start: new Date(),
			title: '123'
		},
		{
			start: new Date('2018/10/20'),
			title: '1fff23'
		},
		{
			start: new Date('2018/09/20'),
			
		},
	];
	constructor() { }

	ngOnInit() {
		//input 日曆標籤
		if(this.inputCalendarEvents)
			this.events = this.inputCalendarEvents;
	}


	ngOnChanges(changes: {[propKey: string]: SimpleChange}) {
		//input 日曆標籤
		this.inputCalendarEvents = changes.inputCalendarEvents.currentValue;
		if(this.inputCalendarEvents)
			this.events = this.inputCalendarEvents;

	    let log: string[] = [];
	    for (let propName in changes) {
	      let changedProp = changes[propName];
	      let to = JSON.stringify(changedProp.currentValue);
	      if (changedProp.isFirstChange()) {
	        log.push(`Initial value of ${propName} set to ${to}`);
	      } else {
	        let from = JSON.stringify(changedProp.previousValue);
	        log.push(`${propName} changed from ${from} to ${to}`);
	      }
	    }

	  }


	dayClicked($event) {
		console.log($event);
		//output data
		this.childEvent.emit($event);
	}

}
