import { Directive, Input } from '@angular/core';
import { NG_VALIDATORS, Validator,  ValidatorFn, FormControl, AbstractControl } from '@angular/forms';

@Directive({
	selector: '[appComfirmPw]',
	providers: [{provide: NG_VALIDATORS, useExisting: ComfirmPwDirective, multi: true}]
})

export class ComfirmPwDirective implements Validator {

	constructor() { }

	@Input('appComfirmPw') pw: string;

	validate(control: AbstractControl): {[key: string]: any} | null {
		let comfirm_pw = control.value;

		return (comfirm_pw==this.pw) ? null : {
      			validatePassword: {
        			valid: false
      			}
      	};
      	
	}

}

export function ComfirmPwValidator(): ValidatorFn {
	return (control: AbstractControl): { [key: string]: any} | null => {
		console.log(control.root);
		console.log(control.root.value.password);
		console.log(control.root.value.password_comfirm);
		//let e = control.root.get(this.validateEqual);
    	return (control.root.value.password==control.root.value.password_comfirm) ? null : {
        			comfirmPw: true
      			}
    };
}

