import { Injectable } from '@angular/core';
import { HttpService } from '../services/http.service';
import  * as Config from '../models/config.model';
import { catchError, map, tap, retry } from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of, throwError } from 'rxjs';
import { formatDate } from '@angular/common';

@Injectable({
  providedIn: 'root'
})

export class MemberService {

	user;
	mailCount;

  	constructor(private http:HttpClient, private httpService:HttpService) { }

  	getUserInfo(): Observable<any> {
		const url = `${Config.APIUrl}/api/user`;
		return this.http.get(url)
		.pipe(
			map(res => { 
				console.log(res);
				return res; 
			})
		);
	}


	//取得(更新)Header的使用者資訊 和 未讀訊息數量
	getUser(){
		const url = `${Config.APIUrl}/api/user`;
		this.httpService.getHttp(url).subscribe(res => {
			this.user = res;
			console.log(res);

			//未讀訊息數量
			this.mailReadCount();
		});
	}


	//取得(更新)Header的使用者資訊 和 未讀訊息數量 
	setUserLogin(data){
		//const url = `${Config.APIUrl}/api/user`;
		//this.httpService.getHttp(url).subscribe(res => {
		this.user = data.user_info;
		this.mailCount = data.mail_notify;
			
	}


	//未讀訊息數量
	mailReadCount(){
		let end_date = formatDate(new Date(), 'yyyy-MM-dd', 'en-US');
		let paramObject = {
			'start_date':'1990-01-01',
			'end_date':end_date,
			'read':0
		};

		let param = this.httpService.getParamFormat(paramObject);
		let mailurl = `${Config.APIUrl}/api/member/mail/count?${param}`;
		this.httpService.getHttp(mailurl).subscribe(
			res =>  this.mailCount = res.count
		);
	}
}
